import * as React from 'react';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Container from '@mui/material/Container';
import IconButton from '@mui/material/IconButton';
import Link from '@mui/material/Link';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';
import SwipeableDrawer from '@mui/material/SwipeableDrawer';
import Typography from '@mui/material/Typography';
import MenuIcon from '@mui/icons-material/Menu';
import { darkTheme } from '../style';
import Logo from '../../images/logo.svg';
import { scrollToTop, scrollToSection } from '../../utils/scroll';

const menuLists = [
  'About',
  'Facility',
  'Pricing',
  'Booking',
  'Trainer',
  'Access',
  'Contact'
];

const MenuLink = props => (
  <Link
    {...props}
    underline='hover'
    sx={{
      margin: '0 1em',
      display: {
        xs: 'none',
        md: 'inline'
      },
      ...props.sx
    }}
  >
    <Typography component='span' variant='body2' color='#fff'>
      {props.children}
    </Typography>
  </Link>
);

const SectionMenu = () => {
  const [isOpen, setIsOpen] = React.useState(false);

  const toggleDrawer = _isOpen => event => {
    if (
      event &&
      event.type === 'keydown' &&
      (event.key === 'Tab' || event.key === 'Shift')
    ) {
      return;
    }

    setIsOpen(_isOpen);
  };

  return (
    <AppBar position='fixed' sx={darkTheme}>
      <Container>
        <Box
          sx={{
            display: 'flex',
            justifyContent: 'space-between',
            alignItems: 'center',
            height: '68px'
          }}
        >
          <Box>
            <Link
              href='#top'
              sx={{
                display: 'block'
              }}
              onClick={e => {
                e.preventDefault();
                scrollToTop();
              }}
            >
              <img
                src={Logo}
                alt='addict gym'
                width={96}
                height={68}
                style={{
                  display: 'block'
                }}
              />
            </Link>
          </Box>
          <Box>
            {menuLists.map((title, i) => {
              const lowerTitle = title.toLowerCase();

              return (
                <MenuLink
                  key={i}
                  href={`#${lowerTitle}`}
                  onClick={e => {
                    e.preventDefault();
                    scrollToSection(lowerTitle);
                  }}
                >
                  {title}
                </MenuLink>
              );
            })}
            <IconButton
              onClick={toggleDrawer(true)}
              sx={{
                display: {
                  xs: 'block',
                  md: 'none'
                }
              }}
            >
              <MenuIcon
                sx={{
                  color: '#fff'
                }}
              />
            </IconButton>
          </Box>
        </Box>
      </Container>

      <SwipeableDrawer
        anchor='right'
        open={isOpen}
        onClose={toggleDrawer(false)}
        onOpen={toggleDrawer(true)}
      >
        <Box
          sx={{
            ...darkTheme,
            width: 200,
            height: '100%'
          }}
          role='presentation'
          onClick={toggleDrawer(false)}
          onKeyDown={toggleDrawer(false)}
        >
          <List>
            {menuLists.map((sectionName, i) => (
              <ListItem
                button
                key={i}
                onClick={e => {
                  e.preventDefault();
                  scrollToSection(sectionName.toLowerCase());
                }}
              >
                <ListItemText
                  primary={<Typography>{sectionName}</Typography>}
                />
              </ListItem>
            ))}
          </List>
        </Box>
      </SwipeableDrawer>
    </AppBar>
  );
};

export default SectionMenu;
